import React from 'react';
import CurrencyInput from 'react-currency-input-field';
import { DptoObj, TypologyObj } from '../../types/types';
const FormPrices = (props: {
	selectedDpto: DptoObj & { typology: TypologyObj };
	selectedGarage:
		| {
				name: string;
				price: number;
				obs?: string | undefined;
				status: number;
				building_id: number;
				createdAt?: string | undefined;
				updatedAt?: string | undefined;
		  }
		| undefined;
	showGarages: boolean;
	discount: number;
	initialCash: number;
	setInitialCash: (arg: number) => void;
	setDiscount: (arg: number) => void;
}) => {
	const {
		selectedDpto,
		selectedGarage,
		showGarages,
		initialCash,
		setInitialCash,
		discount,
		setDiscount
	} = props;
	const classInputCurrency =
		'form-control form-control-lg text-center bg-transparent border-0';

	const totalAmount =
		(selectedDpto?.price || 0) +
		(selectedGarage?.price || 0) -
		discount -
		initialCash;

	return (
		<table className="table table-bordered table-small">
			<tbody>
				<tr>
					<td className={'text-end'}>Precio Dpto:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							id="price-dpto"
							name="input-name"
							placeholder="Please enter a number"
							defaultValue={0}
							groupSeparator={','}
							decimalSeparator={'.'}
							value={selectedDpto?.price}
							decimalsLimit={2}
							prefix={'USD '}
							readOnly
							disabled
						/>
					</td>
				</tr>
				<tr>
					<td className={'text-end'}>Precio Cochera:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							id="price-garage"
							name="input-name"
							placeholder="Please enter a number"
							defaultValue={0}
							value={showGarages ? selectedGarage?.price : 0}
							decimalsLimit={2}
							prefix={'USD '}
							disabled
						/>
					</td>
				</tr>
				<tr>
					<td className={'text-end'}>Propuesta inicial del cliente:</td>
					<td>
						<CurrencyInput
							className={
								'form-control form-control-lg text-center text-primary'
							}
							id="price-garage"
							name="input-name"
							placeholder="Ingrese un monto"
							defaultValue={0}
							value={initialCash}
							decimalsLimit={2}
							prefix={'USD '}
							onValueChange={(e) => {
								if (!e) {
									setInitialCash(0);
								} else {
									setInitialCash(+e);
								}
							}}
						/>
					</td>
				</tr>
				<tr>
					<td className={'text-end'}>Descuento promocional:</td>
					<td>
						<CurrencyInput
							className={'form-control form-control-lg text-danger text-center'}
							id="price-garage"
							name="input-name"
							placeholder="Ingrese un monto"
							defaultValue={0}
							value={discount}
							decimalsLimit={2}
							prefix={'USD - '}
							onValueChange={(e) => {
								if (!e) {
									setDiscount(0);
								} else {
									setDiscount(+e);
								}
							}}
						/>
					</td>
				</tr>
				<tr>
					<td className="text-end">Total:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency + ' text-success'}
							id="price-dpto"
							name="input-name"
							placeholder="Please enter a number"
							defaultValue={0}
							groupSeparator={','}
							decimalSeparator={'.'}
							value={totalAmount}
							decimalsLimit={2}
							prefix={'USD '}
							readOnly
							disabled
						/>
					</td>
				</tr>
			</tbody>
		</table>
	);
};

export default FormPrices;
