import PageBuilding from '../building/pages/PageBuilding';
import PageBuildingCreate from '../building/pages/PageBuildingCreate';
import PageCalc from '../calc/pages/PageCalc';
import PageCalcBolo from '../calc/pages/PageCalcBolo';
import PageBuildingDptos from '../dpto/pages/PageBuildingDptos';
import LayoutAdmin from '../layouts/LayoutAdmin';
import LayoutBolognesi from '../layouts/LayoutBolognesi';
import LayoutPublic from '../layouts/LayoutPublic';
import PageQuoteRegister from '../quote/pages/PageQuoteRegister';
import PageServices from '../service/pages/PageServices';
import { IRoute } from '../types/types';
import PageLogin from '../user/pages/PageLogin';

export enum routesCollection {
	SERVICES = '/services',
	CALCULATOR = '/calculator/:building_id',
	CALCULATOR_BOLO = '/calculadora/proyecto-bolognesi',
	BUILDINGS = '/buildings',
	BUILDINGS_CREATE = '/buildings/create',
	BUILDING_DPTOS = '/buildings/:building_id/dptos',
	QUOTE_REGISTER = '/quote/register',
	HOME = '/'
}
export class RoutesCollection {
	static buildingDptos({ building_id }: { building_id: number }) {
		return `/buildings/${building_id}/dptos`;
	}
}

export const routes: Array<IRoute> = [
	{ path: '/', component: PageLogin, layout: LayoutAdmin },
	{
		path: routesCollection.BUILDING_DPTOS,
		component: PageBuildingDptos,
		layout: LayoutAdmin
	},
	{
		path: routesCollection.QUOTE_REGISTER,
		component: PageQuoteRegister,
		layout: LayoutPublic
	},
	{
		path: routesCollection.SERVICES,
		component: PageServices,
		layout: LayoutAdmin
	},
	{
		path: routesCollection.CALCULATOR_BOLO,
		component: PageCalcBolo,
		layout: LayoutBolognesi
	},
	{
		path: routesCollection.CALCULATOR,
		component: PageCalc,
		layout: LayoutBolognesi
	},
	{
		path: routesCollection.BUILDINGS,
		component: PageBuilding,
		layout: LayoutAdmin
	},
	{
		path: routesCollection.BUILDINGS_CREATE,
		component: PageBuildingCreate,
		layout: LayoutAdmin
	}
];
