import React, { useEffect, useState } from 'react';
import { getServices } from '../../services/servicesservice';
import GenericModal from '../../shared/components/GenericModal';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { ServiceObj } from '../../types/types';
import FormServiceCreate from '../components/FormServiceCreate';
import ServicesTable from '../components/ServicesTable';

const PageServices = () => {
	const [showModal, setShowModal] = useState(false);
	const [services, setServices] = useState<Array<ServiceObj>>([]);
	const [loading, setLoading] = useState(true);

	const getServicesCall = () => {
		setLoading(true);
		getServices().then((data) => {
			setServices(data.content);
			setLoading(false);
		});
	};

	useEffect(() => {
		getServicesCall();
	}, []);
	return (
		<>
			<GenericModal
				show={showModal}
				setShow={setShowModal}
				title="Crear nuevo servicio"
			>
				<FormServiceCreate
					getServicesCall={getServicesCall}
					setShowModal={setShowModal}
				/>
			</GenericModal>

			<div className="row">
				<div className="col">
					<div className="row">
						<div className="col-md-10">
							<h2>Administración de servicios</h2>
						</div>
						<div className="col-md-2">
							<button
								className="btn btn-success"
								onClick={() => setShowModal(true)}
							>
								<i className="fa-solid fa-circle-plus"></i> Crear Servicio
							</button>
						</div>
					</div>
					{loading ? (
						<LoadingComponent
							title="Cargando servicios"
							message="Cargando los servicios de la base de datos"
						/>
					) : (
						<ServicesTable services={services} />
					)}
				</div>
			</div>
		</>
	);
};

export default PageServices;
