import React from 'react';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';
import Select, { components, ActionMeta, MultiValue } from 'react-select';
import Swal from 'sweetalert2';
import {
	deleteDptoService,
	postDptoService
} from '../../services/dptoserviceservice';
import { toast } from 'react-toastify';

interface BuildingDptosTableProps {
	building: BuildingObj & {
		dptos: Array<
			Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
				services: Array<ServiceObj>;
			}
		>;
		services: Array<ServiceObj>;
	};
	refreshBuilding: () => void;
}

const BuildingDptosTable = (props: BuildingDptosTableProps) => {
	const { dptos, services: availableServices } = props.building;
	const { refreshBuilding } = props;

	const handleChange = (
		_newElements: MultiValue<{
			value: number | undefined;
			label: string;
		}>,
		action: ActionMeta<{
			value: number | undefined;
			label: string;
		}>,
		dpto_id: number
	) => {
		if (action.action === 'remove-value') {
			Swal.fire({
				title: '¿Seguro que desea quitar el servicio al departamento?',
				text: 'Podrás agregarlo nuevamente más adelante',
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, borrar!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					deleteDptoService(dpto_id, action.removedValue?.value!).then(
						(data) => {
							if (data.ok) {
								toast(data.content, { type: 'success' });
								refreshBuilding();
							}
						}
					);
				}
			});
		} else if (action.action === 'select-option') {
			Swal.fire({
				title: `¿Seguro que desea asociar el servicio ${action.option?.label}?`,
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, Asociar!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					postDptoService(dpto_id, action.option?.value!).then((data) => {
						if (data.ok) {
							toast('El servicio se ha asociado con éxito', {
								type: 'success'
							});
							refreshBuilding();
						}
					});
				}
			});
		}
	};
	return (
		<table className="table table-hover table-bordered mt-3">
			<thead>
				<tr>
					<th scope="col">#</th>
					<th scope="col">Número</th>
					<th scope="col">Descripción</th>
					<th scope="col">Servicios</th>
					<th scope="col">Acciones</th>
				</tr>
			</thead>
			<tbody>
				{dptos.map((d, idx) => (
					<tr key={idx}>
						<th scope="row">{idx + 1}</th>
						<td>{d.number}</td>
						<td>{d.obs}</td>
						<td>
							<Select
								placeholder="Click para agregar servicios"
								onChange={(newElements, action) =>
									handleChange(newElements, action, d.id!)
								}
								components={{
									IndicatorsContainer: ({ ...props }) => (
										<components.IndicatorsContainer
											{...props}
										></components.IndicatorsContainer>
									)
								}}
								options={availableServices?.map((service) => {
									return { value: service.id, label: service.name };
								})}
								value={d.services.map((service) => {
									return { value: service.id, label: service.name };
								})}
								isMulti
								name="colors"
								isClearable={false}
								classNamePrefix="select"
								styles={{
									control: (provided) => {
										return {
											...provided,
											backgroundColor: 'transparent !important'
										};
									},
									multiValue: (provided) => {
										return {
											...provided,
											borderRadius: '10px'
										};
									}
								}}
							/>
						</td>
						<td>
							<button className="btn btn-secondary me-2">Editar </button>
							<button className="btn btn-danger">Eliminar </button>
						</td>
					</tr>
				))}
			</tbody>
		</table>
	);
};

export default BuildingDptosTable;
