import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutesCollection } from '../../routes/routes';
import { getBuildings } from '../../services/buildingservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';
import Swal from 'sweetalert2';
import Select, { components, ActionMeta, MultiValue } from 'react-select';
import {
	deleteBuildingServiceAssociation,
	postBuildingServiceAssociation
} from '../../services/buildingserviceservice';
import { toast } from 'react-toastify';
import { getServices } from '../../services/servicesservice';
const BuildingTable = () => {
	const [buildings, setBuildings] = useState<
		Array<BuildingObj & { dptos: Array<DptoObj>; services: Array<ServiceObj> }>
	>([]);
	const [loading, setloading] = useState(true);
	const [allServices, setAllServices] = useState<ServiceObj[]>();
	const [loadingServices, setLoadingServices] = useState(true);
	const navigate = useNavigate();

	const getBuildingsCall = () => {
		getBuildings().then((data) => {
			setBuildings(data);
			setloading(false);
		});
	};

	useEffect(() => {
		getBuildingsCall();
	}, []);
	useEffect(() => {
		getServices().then((data) => {
			setAllServices(data.content);
			setLoadingServices(false);
		});
	}, []);

	const handleChange = (
		_newElements: MultiValue<{
			value: number | undefined;
			label: string;
		}>,
		action: ActionMeta<{
			value: number | undefined;
			label: string;
		}>,
		building_id: number
	) => {
		if (action.action === 'remove-value') {
			Swal.fire({
				title: '¿Seguro que desea quitar el servicio al proyecto?',
				text: 'Si existe algún departamento asociado a este edificio con el servicio que intenta eliminar, la asociación también se perderá',
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, borrar!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					deleteBuildingServiceAssociation({
						building_id,
						service_id: action.removedValue?.value!
					}).then((data) => {
						if (data.ok) {
							toast(data.content, { type: 'success' });
							getBuildingsCall();
						}
					});
				}
			});
		} else if (action.action === 'select-option') {
			Swal.fire({
				title: `¿Seguro que desea asociar el servicio ${action.option?.label}?`,
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, Asociar!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					postBuildingServiceAssociation({
						building_id,
						service_id: action.option?.value!
					}).then((data) => {
						if (data.ok) {
							toast('El servicio se ha asociado con éxito', {
								type: 'success'
							});
							getBuildingsCall();
						}
					});
				}
			});
		}
	};

	return loading || loadingServices ? (
		<LoadingComponent
			title="Cargando proyectos"
			message="Cargando proyectos y servicios"
		/>
	) : (
		<table className="table table-bordered mt-3">
			<thead>
				<tr>
					<th scope="col">#</th>
					<th scope="col">Nombre</th>
					<th scope="col">Número de departamentos</th>
					<th scope="col">Servicios disponibles</th>
					<th scope="col">Acciones</th>
				</tr>
			</thead>
			<tbody>
				{buildings.map((b, idx) => (
					<tr key={idx} className="text-center">
						<th scope="row">{idx + 1}</th>
						<td>{b.name}</td>
						<td>{b.dptos.length}</td>
						<td>
							<Select
								placeholder="Click para agregar servicios"
								onChange={(newElements, action) =>
									handleChange(newElements, action, b.id!)
								}
								components={{
									IndicatorsContainer: ({ ...props }) => (
										<components.IndicatorsContainer
											{...props}
										></components.IndicatorsContainer>
									)
								}}
								options={allServices?.map((service) => {
									return { value: service.id, label: service.name };
								})}
								value={b.services.map((service) => {
									return { value: service.id, label: service.name };
								})}
								isMulti
								name="colors"
								isClearable={false}
								classNamePrefix="select"
								styles={{
									control: (provided) => {
										return {
											...provided,
											backgroundColor: 'transparent !important'
										};
									},
									multiValue: (provided) => {
										return {
											...provided,
											borderRadius: '10px'
										};
									}
								}}
							/>
						</td>
						<td>
							<button
								className="btn btn-outline-primary me-2"
								onClick={() => {
									navigate(
										RoutesCollection.buildingDptos({ building_id: b.id! })
									);
								}}
							>
								<i className="fa-solid fa-building me-2"></i>
								Ver dptos
							</button>
							<button className="btn btn-secondary me-2">Editar </button>
							<button className="btn btn-danger">Eliminar </button>
						</td>
					</tr>
				))}
			</tbody>
		</table>
	);
};

export default BuildingTable;
