import { CommonResponse } from './buildingservice';
import { BACK_URL } from './../constants';
import axios from 'axios';
import { BuildingserviceObj } from '../types/types';

export const deleteBuildingServiceAssociation = async ({
	building_id,
	service_id
}: {
	building_id: number;
	service_id: number;
}) => {
	const response = await axios.delete(
		`${BACK_URL}/building-service/building/${building_id}/service/${service_id}`
	);
	return response.data as CommonResponse<string>;
};

export const postBuildingServiceAssociation = async ({
	building_id,
	service_id
}: {
	building_id: number;
	service_id: number;
}) => {
	const response = await axios.post(
		`${BACK_URL}/building-service/building/${building_id}/service/${service_id}`
	);
	return response.data as CommonResponse<BuildingserviceObj>;
};
