import { ServiceObj } from './../types/types';
import { BACK_URL } from './../constants';
import axios from 'axios';
import { CommonResponse } from './buildingservice';

export const getServices = async () => {
	const response = await axios.get(`${BACK_URL}/service`);
	return response.data as CommonResponse<ServiceObj[]>;
};

export const postServiceCreate = async (service: ServiceObj) => {
	const response = await axios.post(`${BACK_URL}/service`, { ...service });
	return response.data;
};
