import { DptoObj } from './../types/types';
import { BACK_URL } from './../constants';
import axios from 'axios';
import { CommonResponse } from './buildingservice';

export const postDptoService = async (dpto_id: number, service_id: number) => {
	const response = await axios.post(
		`${BACK_URL}/dpto-service/dpto/${dpto_id}/service/${service_id}`
	);
	return response.data as CommonResponse<string>;
};

export const deleteDptoService = async (
	dpto_id: number,
	service_id: number
) => {
	const response = await axios.delete(
		`${BACK_URL}/dpto-service/dpto/${dpto_id}/service/${service_id}`
	);
	return response.data as CommonResponse<string>;
};
