import React, { useEffect, useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import Select, { components, ActionMeta, MultiValue } from 'react-select';

import LoadingButton from '../../shared/components/LoadingButton';
import { postBuildingCreate } from '../../services/buildingservice';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import { routesCollection } from '../../routes/routes';
import { getServices } from '../../services/servicesservice';
import { ServiceObj } from '../../types/types';
import LoadingComponent from '../../shared/components/LoadingComponent';
import Swal from 'sweetalert2';
const FormBuildingCreate = () => {
	const [loadingReq, setLoadingReq] = useState(false);
	const [errorReq, setErrorReq] = useState(false);
	const navigate = useNavigate();
	const [services, setServices] = useState<Array<ServiceObj>>([]);
	const [loadingServices, setLoadingServices] = useState(true);
	const [selectedServices, setSelectedServices] = useState<Array<number>>([]);
	useEffect(() => {
		getServices().then((data) => {
			if (data.ok) {
				setLoadingServices(false);
				setServices(data.content);
			}
		});
	}, []);

	const formik = useFormik({
		initialValues: { name: '' },
		validationSchema: Yup.object({
			name: Yup.string().required('El nombre del proyecto es obligatorio')
		}),
		onSubmit: (formData) => {
			Swal.fire({
				title: '¿Seguro que desea crear el proyecto?',
				text: 'Podrá editar la información mas adelante',
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, crear!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					setLoadingReq(true);
					try {
						postBuildingCreate({ name: formData.name }, selectedServices)
							.then((data) => {
								if (data?.ok && data?.content.id) {
									toast('El proyecto ha sido creado correctamente', {
										type: 'success'
									});
									navigate(routesCollection.BUILDINGS);
								} else {
									setErrorReq(true);
									toast(
										'Hubo un problema al crear el proyecto, consulte al administrador',
										{
											type: 'error'
										}
									);
								}
								setLoadingReq(false);
							})
							.catch((_error) => {
								setErrorReq(true);
								setLoadingReq(false);
								toast(
									'Hubo un problema al crear el proyecto, consulte al administrador',
									{
										type: 'error'
									}
								);
							});
					} catch (error) {
						setErrorReq(true);
						setLoadingReq(false);
						toast(
							'Hubo un problema al crear el proyecto, consulte al administrador',
							{
								type: 'error'
							}
						);
					}
				}
			});
		}
	});

	const handleChange = (
		newElements: MultiValue<{
			value: number | undefined;
			label: string;
		}>
	) => {
		setSelectedServices(newElements.map((el) => el.value!));
	};

	return loadingServices ? (
		<LoadingComponent
			title="Cargando servicios"
			message="Cargando todos los servicios disponibles"
		/>
	) : (
		<form onSubmit={formik.handleSubmit}>
			<div className="mb-3">
				<label htmlFor="name" className="form-label">
					Nombre del proyecto:
				</label>
				<input
					type="text"
					className="form-control"
					id="name"
					placeholder="Ejm: Proyecto apolo"
					value={formik.values.name}
					onChange={formik.handleChange}
				/>
				{formik.errors.name ? (
					<small className="text-danger">{formik.errors.name}</small>
				) : null}
			</div>
			<div className="mb-3">
				<label htmlFor="services" className="form-label">
					Seleccione los servicios que tiene el proyecto:
				</label>
				<Select
					placeholder="Click para agregar servicios"
					onChange={(newElements) => handleChange(newElements)}
					options={services?.map((service) => {
						return { value: service.id, label: service.name };
					})}
					// value={b.Services.map((service) => {
					// 	return { value: service.id, label: service.name };
					// })}
					isMulti
					id="services"
					name="colors"
					isClearable={false}
					classNamePrefix="select"
				/>
			</div>
			<div className="mb-3 text-end">
				<LoadingButton
					type="submit"
					className="btn btn-primary"
					loading={loadingReq}
					disabled={Object.keys(formik.errors).length > 0 || loadingReq}
				>
					Crear
				</LoadingButton>
			</div>
		</form>
	);
};

export default FormBuildingCreate;
