import React, { useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import LoadingButton from '../../shared/components/LoadingButton';
import { toast } from 'react-toastify';
import { postServiceCreate } from '../../services/servicesservice';

interface FormServiceCreateProps {
	setShowModal: (value: boolean) => void;
	getServicesCall: () => void;
}
const FormServiceCreate = ({
	getServicesCall,
	setShowModal
}: FormServiceCreateProps) => {
	const [loadingReq, setLoadingReq] = useState(false);
	const [errorReq, setErrorReq] = useState(false);
	const formik = useFormik({
		initialValues: { name: '' },
		validationSchema: Yup.object({
			name: Yup.string().required('El nombre del servicio es obligatorio')
		}),
		onSubmit: (formData) => {
			console.log(formData);

			setLoadingReq(true);
			try {
				postServiceCreate({ name: formData.name })
					.then((data) => {
						if (data?.ok && data?.content.id) {
							toast('El servicio ha sido creado correctamente', {
								type: 'success'
							});
							setShowModal(false);
							getServicesCall();
							setErrorReq(false);
						} else {
							setErrorReq(true);
							toast(
								'Hubo un problema al crear el servicio, consulte al administrador',
								{
									type: 'error'
								}
							);
						}
						setLoadingReq(false);
					})
					.catch((error) => {
						setErrorReq(true);
						setLoadingReq(false);
						toast(
							'Hubo un problema al crear el servicio, consulte al administrador',
							{
								type: 'error'
							}
						);
					});
			} catch (error) {
				setErrorReq(true);
				setLoadingReq(false);
				toast(
					'Hubo un problema al crear el servicio, consulte al administrador',
					{
						type: 'error'
					}
				);
			}
		}
	});

	return (
		<form onSubmit={formik.handleSubmit}>
			{errorReq && (
				<div className="mb-3">
					<div className="alert alert-danger" role="alert">
						Ups! ocurrió un error al traer los servicios
					</div>
				</div>
			)}

			<div className="mb-3">
				<label htmlFor="name" className="form-label">
					Nombre del servicio:
				</label>
				<input
					type="text"
					className="form-control"
					id="name"
					placeholder="Ejm: Servicio de cochera"
					value={formik.values.name}
					onChange={formik.handleChange}
				/>
				{formik.errors.name ? (
					<small className="text-danger">{formik.errors.name}</small>
				) : null}
			</div>
			<div className="mb-3 text-end">
				<LoadingButton
					type="submit"
					className="btn btn-primary"
					loading={loadingReq}
					disabled={Object.keys(formik.errors).length > 0 || loadingReq}
				>
					Crear
				</LoadingButton>
			</div>
		</form>
	);
};

export default FormServiceCreate;
