import React, { useEffect, useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import Select, { components, ActionMeta, MultiValue } from 'react-select';

import LoadingButton from '../../shared/components/LoadingButton';
import { postBuildingCreate } from '../../services/buildingservice';
import { toast } from 'react-toastify';
import { routesCollection } from '../../routes/routes';
import { ServiceObj } from '../../types/types';
import LoadingComponent from '../../shared/components/LoadingComponent';
import Swal from 'sweetalert2';
import { postDptoCreate } from '../../services/dptoservice';

interface FormDptoCreateProps {
	availableServices: ServiceObj[];
	building_id: number;
	setShowModal: (value: boolean) => void;
	refreshBuilding: () => void;
}

const FormDptoCreate = ({
	availableServices,
	building_id,
	setShowModal,
	refreshBuilding
}: FormDptoCreateProps) => {
	const [loadingReq, setLoadingReq] = useState(false);
	const [errorReq, setErrorReq] = useState(false);
	const [selectedServices, setSelectedServices] = useState<Array<number>>([]);

	const formik = useFormik({
		initialValues: { number: '', obs: '' },
		validationSchema: Yup.object({
			number: Yup.string().required('El numero del departamento es obligatorio')
		}),
		onSubmit: (formData) => {
			const { number, obs } = formData;
			Swal.fire({
				title: '¿Seguro que desea crear el departamento y sus asociaciones?',
				text: 'Podrá editar la información mas adelante',
				icon: 'warning',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Sí, crear!',
				cancelButtonText: 'Cancelar'
			}).then((result) => {
				if (result.isConfirmed) {
					setLoadingReq(true);
					try {
						postDptoCreate(
							//TODO agregar el piso
							{ building_id, number, obs, floor: 1, price: 0, status: 0 },
							selectedServices,
							building_id
						)
							.then((data) => {
								if (data?.ok) {
									toast('El departamento ha sido creado correctamente', {
										type: 'success'
									});
									setShowModal(false);
									refreshBuilding();
								} else {
									setErrorReq(true);
									toast(
										'Hubo un problema al crear el proyecto, consulte al administrador',
										{
											type: 'error'
										}
									);
								}
								setLoadingReq(false);
							})
							.catch((_error) => {
								setErrorReq(true);
								setLoadingReq(false);
								toast(
									'Hubo un problema al crear el proyecto, consulte al administrador',
									{
										type: 'error'
									}
								);
							});
					} catch (error) {
						setErrorReq(true);
						setLoadingReq(false);
					}
				}
			});
		}
	});

	const handleChange = (
		newElements: MultiValue<{
			value: number | undefined;
			label: string;
		}>
	) => {
		setSelectedServices(newElements.map((el) => el.value!));
	};

	return (
		<form onSubmit={formik.handleSubmit}>
			{errorReq && (
				<div className="mb-3">
					<div className="alert alert-danger" role="alert">
						Ups! tuvimos un error en el servidor
					</div>
				</div>
			)}
			<div className="mb-3">
				<label htmlFor="number" className="form-label">
					Número del departamento:
				</label>
				<input
					type="text"
					className="form-control"
					id="number"
					placeholder="Ejm: Dpto 102 - A"
					value={formik.values.number}
					onChange={formik.handleChange}
				/>
				{formik.errors.number ? (
					<small className="text-danger">{formik.errors.number}</small>
				) : null}
			</div>

			<div className="mb-3">
				<label htmlFor="obs" className="form-label">
					Observaciones del departamento:
				</label>
				<textarea
					className="form-control"
					id="obs"
					placeholder="Ejm: Dpto fondo 1er piso bloque A"
					value={formik.values.obs}
					onChange={formik.handleChange}
				></textarea>
			</div>

			<div className="mb-3">
				<label htmlFor="services" className="form-label">
					Seleccione los servicios que tiene el departamento:
				</label>
				<Select
					placeholder="Click para agregar servicios"
					onChange={(newElements) => handleChange(newElements)}
					options={availableServices?.map((service) => {
						return { value: service.id, label: service.name };
					})}
					isMulti
					id="services"
					name="services"
					isClearable={false}
					classNamePrefix="select"
				/>
			</div>
			<div className="mb-3 text-end">
				<LoadingButton
					type="submit"
					className="btn btn-primary"
					loading={loadingReq}
					disabled={Object.keys(formik.errors).length > 0 || loadingReq}
				>
					Crear
				</LoadingButton>
			</div>
		</form>
	);
};

export default FormDptoCreate;
