import React, { useMemo } from 'react';
import CurrencyInput from 'react-currency-input-field';
import { DptoObj, GarageObj, TypologyObj } from '../../types/types';

interface FormPaymentListProps {
	initialCash: number;
	paymentsQty: number;
	selectedDpto: DptoObj & { typology: TypologyObj };
	selectedGarage: GarageObj;
	discount: number;
	setPaymentsQty: (arg: number) => void;
}

const FormPaymentList = (props: FormPaymentListProps) => {
	const {
		initialCash,
		paymentsQty,
		setPaymentsQty,
		selectedGarage,
		selectedDpto,
		discount
	} = props;
	const classInputCurrency =
		'form-control form-control-lg text-center bg-transparent border-0';

	const totalAmount = useMemo(() => {
		return (
			(selectedDpto?.price || 0) +
			(selectedGarage?.price || 0) -
			discount -
			initialCash
		);
	}, [selectedDpto, selectedGarage, discount, initialCash]);

	const paymentPerMonth = useMemo(() => {
		return totalAmount / (paymentsQty + 2);
	}, [selectedDpto, selectedGarage, discount, initialCash]);

	return (
		<table className="table table-bordered table-small">
			<tbody>
				<tr>
					<td className="text-end">Cuota Inicial:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							defaultValue={0}
							value={initialCash}
							decimalsLimit={2}
							prefix={'USD '}
							disabled
							readOnly
						/>
					</td>
					<td>
						<i className="fa-solid fa-circle-plus fa-2x"></i>
					</td>
				</tr>
				<tr>
					<td className="text-end">Entrega de bien físico:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							defaultValue={0}
							value={paymentPerMonth}
							decimalsLimit={2}
							prefix={'USD '}
							disabled
							readOnly
						/>
					</td>
				</tr>
				<tr>
					<td className="text-end">Formalización de venta:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							defaultValue={0}
							value={paymentPerMonth}
							decimalsLimit={2}
							prefix={'USD '}
							disabled
							readOnly
						/>
					</td>
				</tr>
				<tr>
					<td className="text-end">Total:</td>
					<td>
						<CurrencyInput
							className={classInputCurrency}
							defaultValue={0}
							value={
								(selectedDpto?.price || 0) +
								(selectedGarage?.price || 0) -
								discount
							}
							decimalsLimit={2}
							prefix={'USD '}
							disabled
							readOnly
						/>
					</td>
				</tr>
			</tbody>
		</table>
	);
};

export default FormPaymentList;
