import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { routesCollection } from '../../routes/routes';
import { getBuildingWithDptos } from '../../services/buildingservice';
import GenericModal from '../../shared/components/GenericModal';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';
import { capitalize } from '../../utils';
import BuildingDptosTable from '../components/BuildingDptosTable';
import FormDptoCreate from '../components/FormDptoCreate';

const PageBuildingDptos = () => {
	const params = useParams();
	const navigate = useNavigate();
	const { building_id } = params;
	const [loadingReq, setLoadingReq] = useState(true);
	const [showModal, setShowModal] = useState(false);
	const [building, setBuilding] = useState<
		BuildingObj & {
			dptos: Array<
				Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
					services: Array<ServiceObj>;
				}
			>;
			services: Array<ServiceObj>;
		}
	>();

	const getBuildingWithDptosCall = () => {
		getBuildingWithDptos(+building_id!)
			.then((data) => {
				if (data.ok && data.content.id) {
					setBuilding(data.content);
					setLoadingReq(false);
				}
			})
			.catch(() => {
				toast('La url del sitio que ha visitado, es incorrecta', {
					type: 'error'
				});
			});
	};

	useEffect(() => {
		if (building_id) {
			setLoadingReq(true);
			getBuildingWithDptosCall();
		} else {
			navigate(routesCollection.BUILDINGS);
			toast('La url del sitio que ha visitado, es incorrecta', {
				type: 'error'
			});
		}
	}, [building_id]);

	return loadingReq ? (
		<LoadingComponent
			title={'Cargando departamentos'}
			message={'Cargando la lista de departamentos'}
		/>
	) : (
		<>
			<GenericModal
				title={'Crear Departamento'}
				setShow={setShowModal}
				show={showModal}
			>
				<FormDptoCreate
					setShowModal={setShowModal}
					building_id={+building_id!}
					availableServices={building?.services!}
					refreshBuilding={getBuildingWithDptosCall}
				/>
			</GenericModal>
			<div className="row align-items-center">
				<div className="col-md-10">
					<h2>Departamentos de: {capitalize(building?.name!)}</h2>
				</div>
				<div className="col-md-2">
					<button
						className="btn btn-success"
						onClick={() => setShowModal(true)}
					>
						<i className="fa-solid fa-circle-plus"></i> Crear Departamento
					</button>
				</div>
			</div>
			<div className="row">
				<div className="col">
					<BuildingDptosTable
						building={building!}
						refreshBuilding={getBuildingWithDptosCall}
					/>
				</div>
			</div>
		</>
	);
};

export default PageBuildingDptos;
