import React, { useMemo, useState } from 'react';
import { getPersonByDni } from '../../services/reniecservice';
import Select, { SingleValue } from 'react-select';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { BuildingsResponse } from '../../services/buildingservice';
import StepWizard from 'react-step-wizard';
import FormStep1DptoInfo from './FormStep1DptoInfo';
import FormResume from './FormResume';
import FormPrices from './FormPrices';
import FormPaymentList from './FormPaymentList';
import { DptoObj, GarageObj, TypologyObj } from '../../types/types';

const availableMails = [
	{ label: '@gmail.com', value: '@gmail.com' },
	{ label: '@hotmail.com', value: '@hotmail.com' },
	{ label: 'other', value: 'other' }
];

const FormQuoteRegister = ({
	buildings
}: {
	buildings: BuildingsResponse[];
}) => {
	const [mailType, setMailType] = useState(availableMails[0].value);
	const [isCash, setIsCash] = useState(false);
	const [SW, setSW] = useState<any>();
	const [showGarages, setShowGarages] = useState(true);
	const [selectedBuilding, setSelectedBuilding] = useState<BuildingsResponse>();
	const [initialCash, setInitialCash] = useState(0);
	const [discount, setDiscount] = useState(0);
	const [selectedDpto, setSelectedDpto] = useState<
		DptoObj & { typology: TypologyObj }
	>();
	const [selectedGarage, setSelectedGarage] = useState<GarageObj>();
	const [paymentsQty, setPaymentsQty] = useState(0);

	const formik = useFormik({
		initialValues: {
			dni: '',
			email: '',
			phone: '',
			name: '',
			date: new Date().toISOString().split('T')[0]
		},
		validationSchema: Yup.object({
			email: Yup.string().required('El email es obligatorio')
		}),
		onSubmit: (formData) => {
			const { dni, email } = formData;
			const completeEmail = email.concat(mailType);
			console.log(completeEmail);
			console.log(formData);
		}
	});

	const getPersonByDniCall = () => {
		getPersonByDni(formik.values.dni).then((data) => {
			console.log(data);
			formik.setFieldValue('name', data.content);
		});
	};
	const handleChangeSelectMail = (
		newValue: SingleValue<{
			label: string;
			value: string;
		}>
	) => {
		setMailType(newValue?.label!);
	};

	const setInstance = (SW: any) => setSW(SW);

	const urlTypology = useMemo(() => {}, [selectedDpto]);

	return (
		<>
			<div className="card shadow">
				<div className="card-header">
					<h5 className="text-center">DATOS DEL PROYECTO</h5>
				</div>
				<div className="card-body">
					<StepWizard instance={setInstance} className="mb-3">
						<FormStep1DptoInfo
							selectedDpto={selectedDpto!}
							selectedGarage={selectedGarage!}
							showGarages={showGarages}
							setShowGarages={setShowGarages}
							SW={SW}
							setSelectedGarage={setSelectedGarage}
							buildings={buildings}
							setSelectedDpto={setSelectedDpto}
							selectedBuilding={selectedBuilding!}
							setSelectedBuilding={setSelectedBuilding}
							isCash={isCash}
							setIsCash={setIsCash}
						/>
						<form autoComplete="off">
							<div className="row g-0 mb-3">
								<div className="col-md-8">
									<div className="form-floating">
										<input
											name="dni"
											id="dni"
											maxLength={8}
											className="form-control form-control-lg"
											type="text"
											value={formik.values.dni}
											onChange={formik.handleChange}
											placeholder="Ejm: 12345678"
										/>
										<label htmlFor="dni" className="form-label">
											Ingrese el dni:
										</label>
									</div>
								</div>
								<div className="col-md-4">
									<button
										className="btn btn-secondary btn-lg w-100 h-100"
										type="button"
										onClick={getPersonByDniCall}
									>
										<i className="fa-solid fa-magnifying-glass me-2"></i>Buscar
										DNI
									</button>
								</div>
							</div>

							<div className="form-floating mb-3">
								<input
									name="name"
									id="name"
									className="form-control form-control-lg"
									type="text"
									value={formik.values.name}
									onChange={formik.handleChange}
									placeholder="Ejm: jorge"
								/>
								<label htmlFor="name" className="form-label">
									Nombre completo:
								</label>
							</div>

							<div className="row  mb-3">
								<div className="col-md-6">
									<div className="form-floating">
										<input
											name="email"
											id="email"
											className="form-control form-control-lg"
											type="email"
											value={formik.values.email}
											onChange={formik.handleChange}
											placeholder="Ejm: jorge"
										/>
										<label htmlFor="email" className="form-label">
											Ingrese el email:
										</label>
									</div>
								</div>
								<div className="col-md-6">
									<Select
										options={availableMails}
										className="form-control h-100"
										onChange={handleChangeSelectMail}
										placeholder="Seleccione email"
									/>
								</div>
							</div>

							<div className="row mb-3">
								<div className="col-md-6">
									<div className="form-floating">
										<input
											name="phone"
											id="phone"
											className="form-control form-control-lg"
											type="text"
											value={formik.values.phone}
											onChange={formik.handleChange}
											placeholder="Ejm: 974352615"
										/>
										<label htmlFor="phone" className="form-label">
											Ingrese el teléfono:
										</label>
									</div>
								</div>
								<div className="col-md-6">
									<div className="form-floating">
										<input
											disabled
											name="date"
											id="date"
											className="form-control form-control-lg"
											type="date"
											value={formik.values.date}
											onChange={formik.handleChange}
											placeholder="Ejm: 974352615"
										/>
										<label htmlFor="date" className="form-label">
											Fecha:
										</label>
									</div>
								</div>
							</div>

							<div className="row mb-3">
								<div className="col">
									<button
										className="btn btn-primary"
										type="button"
										onClick={() => SW?.previousStep()!}
									>
										Anterior
									</button>
								</div>
							</div>
						</form>
					</StepWizard>
				</div>
			</div>
			<hr />
			<div className="card shadow">
				<div className="card-header">
					<h5 className="text-center">RESUMEN DE DPTO</h5>
				</div>
				<div className="card-body">
					<FormResume
						showGarages={showGarages}
						selectedDpto={selectedDpto!}
						selectedGarage={selectedGarage!}
					/>
				</div>
			</div>
			<hr />
			<div className="card shadow">
				<div className="card-header">
					<h5 className="text-center">PRECIO</h5>
				</div>
				<div className="card-body">
					<FormPrices
						discount={discount}
						setDiscount={setDiscount}
						initialCash={initialCash}
						setInitialCash={setInitialCash}
						selectedDpto={selectedDpto!}
						selectedGarage={selectedGarage}
						showGarages={showGarages}
					/>
				</div>
			</div>
			<hr />

			{isCash && (
				<div className="card shadow">
					<div className="card-header">
						<h5 className="text-center">PAGO FRACCIONADO AL CONTADO</h5>
					</div>
					<div className="card-body">
						<FormPaymentList
							initialCash={initialCash}
							paymentsQty={paymentsQty}
							setPaymentsQty={setPaymentsQty}
							discount={discount}
							selectedDpto={selectedDpto!}
							selectedGarage={selectedGarage!}
						/>
					</div>
				</div>
			)}
		</>
	);
};

export default FormQuoteRegister;
