import React from 'react';
import { DptoObj, TypologyObj } from '../../types/types';

const FormResume = (props: {
	selectedDpto: DptoObj & { typology: TypologyObj };
	selectedGarage: {
		name: string;
		price: number;
		obs?: string | undefined;
		status: number;
		building_id: number;
		createdAt?: string | undefined;
		updatedAt?: string | undefined;
	};
	showGarages: boolean;
}) => {
	const { selectedDpto, selectedGarage, showGarages } = props;

	return (
		<>
			<div className="row mb-3">
				<div className={showGarages ? 'col-md-4' : 'col-md-6'}>
					<label htmlFor="typologies" className="form-label">
						Tipología:
					</label>
					<input
						disabled
						name="typology"
						id="typology"
						className="form-control form-control-lg"
						type="text"
						defaultValue={selectedDpto?.typology?.name}
					/>
				</div>
				{showGarages && (
					<div className="col-md-4">
						<label htmlFor="typologies" className="form-label">
							Garages:
						</label>
						<input
							disabled
							readOnly
							name="typology"
							id="typology"
							className="form-control form-control-lg"
							type="text"
							onChange={() => {}}
							value={selectedGarage?.name || ''}
						/>
					</div>
				)}
				<div className={showGarages ? 'col-md-4' : 'col-md-6'}>
					<label htmlFor="floors" className="form-label">
						Nivel:
					</label>
					<input
						disabled
						name="floor"
						id="floor"
						className="form-control form-control-lg"
						type="text"
						defaultValue={selectedDpto?.floor}
					/>
				</div>
			</div>
		</>
	);
};

export default FormResume;
