import { DptoObj } from './../types/types';
import { BACK_URL } from './../constants';
import axios from 'axios';
import { CommonResponse } from './buildingservice';

export const postDptoCreate = async (
	objDpto: DptoObj,
	services: number[],
	building_id: number
) => {
	const response = await axios.post(`${BACK_URL}/dpto`, {
		...objDpto,
		services,
		building_id
	});
	return response.data as CommonResponse<string>;
};
